import Slider from "react-slick";
import {Box, Heading} from "@chakra-ui/react"
import ToolsData from "../Components/ToolsData";


const ToolsImg = [
    {src:'data:image/png;base64,' , name : 'Slack'},
    {src:'data:image/png;base64,' , name : 'Vercel'},
    {src:'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRCU9cs-Li0sdiiukArmYSRvahJn22BqCZXx96TxrQrtTZAUlo_VNhR02Z1G7IK226YyUE&usqp=CAU' , name : 'VS Code'},
    {src:'data:image/png;base64,' , name : 'GitHub'},
    {src:'data:image/png;base64,' , name : 'Netlify'},
    {src:'data:image/png;base64,',name:'Mongo DB'}
]

function Tools() {
    const settings = {
        dots: false,
        infinite: true,
        slidesToShow: 5,
        slidesToScroll: 1,
        autoplay: true,
        speed: 2000,
        autoplaySpeed: 2000,
        cssEase: 'linear',
        responsive: [
            {
              breakpoint: 1024,
              settings: {
                slidesToShow: 3,
                slidesToScroll: 1,
                infinite: true,
                dots: false
              }
            },
            {
              breakpoint: 600,
              settings: {
                slidesToShow: 3,
                slidesToScroll: 1,
                initialSlide: 3
              }
            },
            {
              breakpoint: 480,
              settings: {
                slidesToShow: 1,
                slidesToScroll: 1
              }
            }
          ]
      };





  return (
    <Box id="skills" h={"fit-content"} bg={"#cbd5e0"}>
      <hr />
      <Heading textAlign={"center"} color={"blue.600"}>
        Toolls
      </Heading>
      <Slider {...settings}>
        {ToolsImg.map((el) => (
          <ToolsData key={el.src} src={el.src} name={el.name} />
        ))}
      </Slider>
    </Box>
  );
}

export default Tools